@media print {
  /* Hack to make sure that content is not being cut off on the bottom of a page. This is primarily an issue in IE. */
  .productTeaser,
  .split50 > *,
  .divPaymentsService .payments .row iframe,
  .divCompare,
  .divCompare .ng-scope,
  .divCompare .ng-scope .compareconsumption,
  .divCompare .ng-scope .compareconsumption,
  .divCompare .ng-scope .compareconsumption iframe,
  main > section,
  .row,
  main > section > .row > [class*='col'] {
      display: table !important;
      width: 100% !important;
      max-width: 100% !important;
  }

  .teaser {
      display: table-row;
      .teaser-content,
      .teaser-content > * {
          display: table-row;
          width: 100%;
          max-width: 100%;
      }
  }

  #page {
      width: 100%;
      margin: 0;
      padding: 0;
      background: none;
  }

  body,
  .btn,
  .btn-dropdown,
  .btn-dropdown select,
  .customSelect,
  .customSelect select,
  .customSelect:not(.customSelect-inline),
  .btn-dropdown:before,
  .customSelect:not(.customSelect-inline):before,
  .theme-color-base,
  .customSelect select,
  .theme-color-accent,
  .link-arrow,
  .teaser .teaser-content,
  .heroSlider .heroSlider-content {
      color: black !important;
  }

  header,
  footer,
  .topTasks,
  .headerClassic-spacer,
  .topImage-media,
  .heroOverlay-media,
  .teaser-media,
  .livestream-media,
  .heroSlider-media  {
      display: none !important;
      height: 0 !important;
  }

  .col-was-in-viewport,
  .col-in-viewport-inited{
      transform: none !important;
      transition: none !important;
      opacity: 1 !important;
  }

  #printed-article {
      border: 1px solid #666;
      padding: 10px;
  }

  .onward-links{
      display: none;
      height: 0;
  }

  .exploration {
      .exploration-details {
          .details-left{
              display: none !important;
              height: 0 !important;
          }
          .details-right-content {
              > * {
                  max-height: none;
              }

              &:after {
                  display: none;
              }
          }

          .details-right-content-show-btns {
              display: none;
          }
      }
  }

  .divConsumption, /* Power SS charts*/
  .mapw /* Activity map*/ {
      > * {
          -webkit-print-color-adjust: exact !important; /* Chrome, Safari */
          color-adjust: exact !important; /*Firefox*/
      }
  }

  .divConsumption{
      padding-bottom:100px;
  }

  .icon,
  .accordion-item-trigger {
      &:before {
          display: none !important;
      }
  }

  .splash{
      .splash-inner{
          position:static;
          max-width:none;
          width:auto;
          padding-bottom:inherit;
          border-radius:initial;
      }
      .splash-content{
          position:static;
          width:auto;
          max-height:none;
          transform:initial;
          top:initial;
          left:initial;
      }
  }

  /* Siteimprove */
  .szc_info{
      display:none;
  }

  .heroSlider .heroSlider-content,
  .heroOverlay .heroOverlay-content {
      width: 100% !important;
  }
  .heroOverlay{
      padding: 0 0 !important;
  }

  .multiNarrative {
      max-height: none;
      display: table;
      width: 100%;

      .narrative-content {
          width: 100%;
          max-width: none;
          display: inline-block;
          
          .narrative-body {
              display:inline-block;
          }

          .narrative-text {
              display: table;

              .narrative-cta {
                  display: table;
              }
          }
      }

      .narrative-picker,
      .narrative-media {
          display: none;
      }
  }

  .orderflow {
      .stepindicator {
          padding: 0;
          height: 114px;
      }
  }


  .teaserCircle,
  .form-group,
  .energyMarketChartContainer,
  .orderflow .product {
      page-break-inside: avoid;
  }

  .teaserCircle{
      overflow:visible;
  }

  .marketcontainer {
      .form, form {
          display: table !important;
          width: 100% !important;
          max-width: 100% !important;
      }
  }

  @media (max-width: 768px) {
      .table:not(.table-no-collapse) td, .richtext table:not(.table-no-collapse) td, .gnw_table:not(.table-no-collapse) td {
          display: table-cell;
      }
  }
}

@import "./prints/job";
