$blue: #4099da;

$text-color: #3b4956;
$heading-color: $blue;

@media print {
  main {
    display: block;
    max-width: none;
    width: 100%;

    .container-fluid .row div[class*="col"] {
      display: block !important;
    }

    .container-fluid .row div[class*="col"] > div:not(.job) {
      display: none !important;
      visibility: hidden;
      position: absolute;
      width: 0;
      height: 0;
      top: 0;
      left: 0;
    }

    > .row {
      display: block !important;
    }

    > .row > div[class*="col"] {
      display: block !important;
    }
  }

  .job {
    display: block !important;

    &__article {
      display: block;
      overflow: visible;
      margin: 0;

      .job__aside {
        display: block !important;
        max-width: none;
        width: 33%;
        padding: 0;
        position: absolute;
        right: 0px;
        top: 0;

        .job__aside-inner {
          ul li:before {
            top: 0;
          }
        }
        
        &.hidden-lg-up {
          display: none !important;
        }

        & .job__aside-list.hidden-md-down {
          display: block !important;
        }
      }

      // ul {
      //   margin-left: 0;
      //   padding-left: 0;

      //   li {
      //     padding-left: 16px;
      //     position: relative;

      //     &:before {
      //       position: absolute;
      //       left: 0;
      //       margin-left: 0;
      //       font-size: 16px;
      //     }
      //   }
      // }
    }

    &__article-content {
      display: block !important;
      position: relative;
    }

    .breadcrumbs-container-ov {
      display: none;
    }

    &__image-container {
      display: none !important;
    }

    &__content {
      display: block !important;
    }

    &__content-inner {
      display: block !important;
      padding: 0;
      max-width: none;
      margin-left: unset;
      margin-right: unset;
      color: $text-color;

      &:after {
        clear: both;
        display: block;
        height: 0;
        content: "";
      }
    }

    &__header, &__sub-header {
      line-height: 1.2;
      font-family: "OrstedSansRegular", sans !important;
    }

    &__sub-header {
      font-size: 16px;
      color: $text-color;
      margin-bottom: 18px;
    }

    &__header {
      color: $heading-color;
      font-size: 40px;
      margin-bottom: 40px;
    }

    & &-details-apply.btn {
      display: none;
    }

    & &__content-column {
      p:first-child em {
        font-size: 25px;
        line-height: 1.3;
        margin-bottom: 18px;
        color: $text-color;
        font-family: "OrstedSansRegular", sans !important;
      }
    }

    &__content-column {
      overflow: visible;
      display: block !important;
      width: 65%;
      padding-left: 0;

      ul {
        li {
          padding-left: 32px;
        }
      }
    }

    & &__aside-header {
      font-family: "OrstedSansRegular", sans !important;
      font-size: 21px;
      line-height: 1.2;
      margin-bottom: 8px;
    }

    &__aside-inner {
      padding: 1px 0px;
      margin: 0;
      background-color: unset;
    }

    &__aside-title--video {
      display: none;
      visibility: hidden;
      height: 0;
    }

    &__aside-video {
      display: none;
      visibility: hidden;
      height: 0;
    }

    &__aside-list {
      margin-bottom: 18px;
    }

    &__aside-list-item {
      display: block;

      strong {
        display: inline;
        font-size: 16px;
        float: none;
      }
      
      .job-details-box-date {
        display: inline;
        font-size: 16px;
        float: none;
      }
    }

    &__aside-benefit {
      margin-bottom: 12px;
      min-height: 30px;
      display: block !important;
      position: relative;

      > p {
        padding-top: 4px;
        margin-bottom: 0 !important;
        line-height: 1.2;
        padding-left: 42px;
      }

      img.job__aside-benefit-image {
        width: 30px;
        height: 30px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    
    &__btns {
      display: none;
      visibility: hidden;
      height: 0;
    }
  }
  
  .job-page-wrapper .container-fluid.wide-container {
    display: block !important;

    > .row {
      display: block !important;
    }
  }
}
